
.game {
  display: flex;
  flex-direction: column;
}

.main {
  height: 60vh;
  height: calc(var(--vh, 1vh) * 60);
}

.statusHandler, .statusHandler button {
  width: 100%;
  /*height: var(--gameHeight);*/
  height: inherit;
  position: absolute;
  display: block;
  background-color: transparent;
  border: none;
  outline: none;
}

.previousStatus {
/*  opacity: 0;
  transition: opacity 1s ease-out;*/
}

.canEndTurn {
  opacity: 0;
  /*background: linear-gradient(0deg, rgba(105,255,162,1) 0%, rgba(105,255,162,1) 34%, rgba(178,255,156,1) 100%);*/
  box-shadow: inset 0 0 0 calc(var(--vh, 1vh) * 1.5) rgb(248 248 248 / 100%);
}
  .canEndTurn:hover {
    cursor: pointer;
    /*box-shadow: inset 0 0 0 10px #f8f8f8;*/
  }
  .canEndTurn:active {
  }

.endTurn {
  /*background: linear-gradient(0deg, rgba(105,255,162,1) 0%, rgba(105,255,162,1) 34%, rgba(178,255,156,1) 100%);*/
  box-shadow: inset 0 0 0 calc(var(--vh, 1vh) * 1.5) rgb(248 248 248 / 80%);
}

.redPlayerTurn {
  /*background: linear-gradient(0deg, #ffb58f 0%, #ffca8f 34%, #ffb58f 100%);*/
  background: linear-gradient(0deg, hsl(15deg 70% 62%) 0%, hsl(20deg 90% 69%) 34%, hsl(15deg 80% 62%) 100%);
  /*background: linear-gradient(0deg, #ffc518 0%, #fbe73d 34%, #ffc518 100%);*/
  background: linear-gradient(90deg, #ff5f6d 0%, #ff5f6d 15%, #ffc371 85%, #ffc371 100%);
  opacity: 0.85;
}

.bluePlayerTurn {
  background: linear-gradient(0deg, rgb(105 210 255) 0%, rgb(29 230 206) 34%, rgb(105 210 255) 100%);

  background: linear-gradient(90deg, #5b86e5 0%, #36d1dc 100%);  
  background: linear-gradient(90deg, #03e2d3 0%, #03e2d3 15%, #185a9d 85%, #185a9d 100%);
  opacity: 0.85;
}

.board {
  margin: 0 auto;
  transform: var(--boardStartPos);  
  width: var(--boardWidth);
  height: var(--boardHeight);
}
.main_board {
  position: relative;
  top: 50%;
  /*margin-top: calc(var(--vh, 1vh) * 3.5);*/
  transform: translateY(-50%) var(--boardStartPos);
  box-shadow: 0 0 20px rgba(0,0,0,0.15);
}
.main_board + .title {
  display: none;
}

.board_row {

}

.board_row:after {
  clear: both;
  content: "";
  display: table;
}


.slot {
  /*background: rgba(105,255,247,0.1);*/
  background: transparent;
  /*border: 2px solid rgba(105,255,247,0.5);*/
  /*border: 2px solid rgba(178,255,156,0.3);*/
  border: 2px solid #f8f8f8;
  float: left;
  /*font-size: var(--fontSize);*/
  font-weight: bold;
  color: #333;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  height: var(--slotSize);
  width: var(--slotSize);
}
.main:not(.disabledBoard) .slot:not(.slotFilled):hover {
  background: rgba(105,255,247,0.50);
  cursor: pointer;
}
/*.disabledBoard .slot:hover {
  background: transparent;
  cursor: default;
}*/
/*.slot.filled {

}*/

.slot:focus {
  outline: none;
}

.slotFilled div {
  margin: 0 auto;
  height: calc(var(--slotSize) * 0.8);
  width: calc(var(--slotSize) * 0.8);  
  position: relative;
  transform: translate(0,var(--slotStartPos));
  border-radius: 50%;
}

.slotFilledX div {
  background-color: #eb5047;
}
.slotFilledO div {
  /*background-color: #34807C;*/
  background-color: #82C3FF;
}


.game_info {
  /*height: 20vh;*/
  /*min-height: 100px;*/

}

.title {
  margin: 5px 0;
  font-size: var(--fontSize);
}

.controls {
  height: 5vh;
  height: calc(var(--vh, 1vh) * 5);
}

.status {
  height: 5vh;
  height: calc(var(--vh, 1vh) * 5);
}

.moves {
  display: flex;
  /*flex-wrap: wrap;*/
  justify-content: flex-start;
  padding-left: 0;
  list-style-type: none;
}

.moves li {
  margin-right: 24px;
}

.moves_board {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
  .moves_board .slot {
    border-width: 1px
  }

.highlighted {
  color: #fff;
  background: rgb(252,157,20);
  background: linear-gradient(9deg, rgba(252,157,20,1) 6%, rgba(217,62,10,1) 80%);
  box-shadow: 0 0 10px rgba(234,110,15,0.6);
}

.selected {
  box-shadow: 0 0 10px rgba(0,0,0,0.3);
}

/*.win {
  background-color: #105599;
  color: #fff;
  border-color: #105599
}*/

.kbd-navigation .slot:focus {
  background: #ddd;
}

.scrollableX {
  overflow-y: hidden;
  overflow-x: scroll;
  height: 20vh;
  height: calc(var(--vh, 1vh) * 20);
  background-color: #a6a6a6;  
}

.flipR .board_row{
  float: left;
}
.flipR .slot{
  display: block;
  float: none;
}

.hidden {
  display: none;
}

/*////////// ANIMATIONS ////////////*/
@media (prefers-reduced-motion: no-preference) {
  .win {
    background-color: rgb(248 248 248 / 50%);
  }

  .win > div {
    animation-duration: .4s;
    animation-name: scaleAnim;
    animation-iteration-count: infinite;
    animation-direction: alternate;
  }
  .floating {
    animation: Slot-floating infinite 2.6s ease-in-out;
  }
}

@keyframes Slot-floating {
  0% {
    transform: translateY(-3%);
  }
  50% {
    transform: translateY(3%);
  }
  100% {
    transform: translateY(-3%);
  }  
}
@keyframes scaleAnim {
  from {
    transform: scale(0.8);
  }

  to {
    transform: scale(1);
  }
}

/*////////// GAME TRANSITIONS ////////////*/
/* Rethink the gravity movement with transform, and use transform(--var(a),--var(b)) to input the correct direction*/
.transition.fadeInOnHover{
  transition: 0.25s opacity ease-in;  
  opacity: 0.4;
}
  .transition.fadeInOnHover:hover {
    opacity: .8;
  }
  .fadeInOnHover.hasTransition {
    transition: 0.25s opacity ease-in;  
    opacity: .4;
  }  
  .fadeInOnHover.hasTransition:hover {
    opacity: .8;
  }
/*.transition.saturate {
  opacity: 1;
  transition: 0.25s opacity ease;  
}*/

.transition.main_board.flip {
  transform: translateY(-50%) rotateZ(0deg);
  transition: 0.8s transform ease;  
}
  .main_board.flip.hasTransition {
    transform: translateY(-50%) rotateZ(0deg);
  }

.transition.flip {
  transform: rotateZ(0deg);
  transition: 0.8s transform ease;  
}
  .flip.hasTransition {
    transform: rotateZ(0deg);
  }

.transition.fadeout {
  opacity: 0;
  transition: 0.25s opacity ease;  
}
  .fadeout.hasTransition {
    opacity: 0;
  }

.transition.fall {
  transform: translate(0,0);
  transition-duration: var(--speed);
  transition-property: transform; 
  transition-timing-function: cubic-bezier(.14,0,.27,0);
  /*transition-delay: 1s;*/
}
  .fall.hasTransition {
    transform: translate(0,0);
  }

/*//////////// MEDIA QUERIES /////////////*/
@media (max-width: 1140px) {
  .redPlayerTurn {
    background: linear-gradient(90deg, #ff5f6d 0%, #ffc371 100%);
  }

  .bluePlayerTurn {
    background: linear-gradient(90deg, hsl(176deg 97% 45%) 0%, #185a9d 100%);
  }
}